#about {
    background-color: var(--color-h2);
    padding: 3rem 0;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
    45deg, 
    transparent, 
    var(--color-dark), 
    transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);    
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.about__card {
   background: var(--color-bg);

    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__icon {
    color: var(--color-lighter);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
    color: var(--color-h2);
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-lighter);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-dark);
}

.about__story > h2 {
    color: var(--color-bg);
}

.about__story > p {
    font-family: 'Poor Story', cursive;
    font-size: 1.15rem;
    text-align: justify;
    color: var(--color-bg);
}

.about__story-part-2 {
    font-family: 'Poor Story', cursive;
    font-size: 1.15rem;
    width: 70%;
    margin: 20px auto;
    text-align: justify;
    color: var(--color-bg);
}

.right-img {
    width: 5rem;
    height: 10rem;
    float: right;
}
.left-img {
    width: 5rem;
    height: 10rem;
    float: left;
}

/* ===================  (MEDIUM DEVICES) ======================== */
@media screen and (max-width: 1024px) {
   .about__container {
    grid-template-columns: 1fr;
    gap: 0;
   }

   .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
   }

   .about__content p {
    margin: 1rem 0 1.5rem;
   }
}

/* ===================  (SMALL DEVICES) ======================== */
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;

    }

    .about__cards {
        grid-template-columns:  1fr;
        gap: 1rem;
        padding-left: 2.75rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
       margin: 1.5rem 0;
    }

}