nav {
    background: rgba(0, 0, 0, 0.2);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position:fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 2rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-bg);
    font-size: 1.2rem;
}

nav a:hover {
    background: rgba(0,0,0,0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-white);
}
 
 /* ===================  (SMALL DEVICES) ======================== */
 @media screen and (max-width: 600px) {
    nav a {
        font-size: 0.6rem;
    }
    nav { 
        padding: 0.5rem 0.1rem;
        gap: 0.2rem;
    }
 }